.button-dropdown-container button {
  background-color:#ffffff;
  position: relative;
  display: inline-block;

  font-size: 16px;
  font-weight: bold;
 
  border-radius: 8px;
  color: #000000;
  box-shadow: 0 0 5px #dd0000;


  
 
  padding: 12px 36px 12px 24px;

}

.button-dropdown-container button::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 10px;
  transform: translateY(-50%);
  border-top: 6px solid #000000;
  border-right: 6px solid transparent;
  border-left: 6px solid transparent;
}


.button-dropdown-container button:hover {
  box-shadow: 0 0 10px #dc2c00;
}

.button-dropdown-options {
  position: absolute;
  z-index: 1;
  top: 40px;
  left: 0;
  background-color: #f1f1f1;
  max-height: 200px;
  overflow-y: scroll;
  width: 100%;
  border: 1px solid #ddd;
}

.button-dropdown-options div {
  color: #000;
  padding: 10px;
  font-size: 16px;
  cursor: pointer;
}

.button-dropdown-options div:hover {
  background-color: #ddd;
}








